// MENU DE NAVIGATION
.sidebar {
    height: 100%;
    min-height: 200px;
    min-width : 264px;
    position: relative;
    display: grid;
    grid-template-columns: 1fr ;
    grid-template-rows: 150px;
    grid-template-areas: 
        "1"
    ;
}
.nav-menu {
    grid-area: 1;


    
ul{
    height: 50%;
    display: grid;
    padding-left: $padding-xsmall;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-areas: 
        "a aa . ."
        "b bb bbb ."
        "c cc ccc ."

        ;
    li{
        padding: .2rem 0 .2rem .2rem;
        position: relative;


        span {

                
            
            color: $text-color-white;

            &:hover{
                
                border-radius: 1fr;
                text-decoration-line:underline;
                color: $text-color-lightgrey;
                transition: margin-right 0.3s ease-in-out;
            }

        }

    }

}

}

.id-NavMenu{

    display: flex;
    flex-direction: row;

    @media screen and (max-width : $A4-width) {
        display: flex !important;
        justify-content: center;
    }

    
    @media screen and (max-width : $bp_min) {
        display: flex !important;
        justify-content: center;
        flex-wrap: nowrap;
        margin: 0;
    }
}

#all{

    @media screen and (max-width : $bp_min) {
       display: none;
    }


}

// BAR DE NAVIGATION
#AppBar  {

    width: $A4-width !important;
    justify-content: center;

    @media screen and (max-width : $A4-width) {
        display: flex !important;
        flex-direction: row;
        margin-left: $margin-xsmall;        
        margin-right: $margin-xsmall; 
        justify-content: space-around;
    }

}
// inherit material ui


.MuiPaper-elevation4{
    box-shadow: none !important;
}

.MuiAppBar-positionFixed {
    right:auto !important;
}
.MuiButton-root{
    color: $text-color-white !important;
    text-transform: none !important;
}
#DehazeIcon{
    padding-right: $padding-xxsmall;
}
.fa-globe{
    padding-left: $margin-small;
}

#special_project{
    color: $text-color-lightgrey !important;
    margin-right: $margin-large;
}


.MuiList-root .fas, .MuiList-root a {

    background-color:white;
    color: $celeste-blue;

  
}

.nav-Item{
    text-align: center;
    white-space: nowrap;
    letter-spacing: 0.06em;
    color: black;
    background: #f0f2f5;
    border-radius: 4px;
    border: 1px solid $text-color-lightgrey;
    padding: 6px 8px;
    font-size: 10px;
    line-height: 12px;
    text-transform:uppercase;
    margin-right: 1.5em;

}